<template>
  <section id="game" class="row">
    <canvas id="canvas" width="640" height="1000">
    </canvas>
    <div id="hud">
      <div>Lives: <span id="lives"></span></div>
      <div>E: <span id="energies"></span></div>
      <div>Waves: <span id="waves"></span></div>
    </div>
    <div class="add-buttons">
      <a class='add-button' title='space Junk' data-type='SpaceJunk'>J</a>
      <a class='add-button' title='Satellite' data-type='Satellite'>S</a>
      <a class='add-button' title='Satellite+' data-type='Satellite2'>S+</a>
      <a class='add-button' title='Castle' data-type='Castle'>C</a>
      <a class='add-button' title='Castle+'' data-type='Castle2'>C+</a>
    </div>
  </section>
</template>

<script>
  export default {
    data () {
      return {
        gameWidth: 640,
        gameHeight: 1000,
        cjs: null,
        canvas: null,
        stage: null,
        lives: 20,
        energies: 99999,
        assetsLib: null,
        enemyList: [],
        energyList: [],
        satelliteList: [],
        bulletList: [],
        castleList: [],
        waves: {}
      }
    },

    mounted () {
      this.init()
    },

    methods: {
      init () {
        this.cjs = window.createjs
        this.assetsLib = window.assetsLib
        // change 3
        this.images = window.images
        this.canvas = document.getElementById('canvas')
        this.stage = new this.cjs.Stage(this.canvas)

        this.bgLayer = new this.cjs.Container()
        this.bgLayer.addChild(new this.assetsLib.Background())
        this.stage.addChild(this.bgLayer)

        this.boardLayer = this.Board()
        this.stage.addChild(this.boardLayer)

        this.effectLayer = new this.cjs.Container()
        this.stage.addChild(this.effectLayer)

        this.setHud()
        this.stage.on('click', this.stageClick)

        // this.initWaves()
        // this.startWave()
        // change 4
        this.load()

        this.cjs.Ticker.setFPS(40)
        this.cjs.Ticker.addEventListener('tick', this.stage)
        this.cjs.Ticker.addEventListener('tick', this.tick)
      },
      // change 5
      load () {
        var loader = new this.cjs.LoadQueue(false)
        loader.addEventListener('fileload', function (e) {
          if (e.item.type === 'image') {
            // 将彩色图片加载到assets库
            this.images[e.item.id] = e.result
          }
        }.bind(this))
        loader.addEventListener('complete', this.start)
        loader.loadManifest(this.assetsLib.properties.manifest)
      },
      start () {
        this.initWaves()
        this.startWave()
      },
      initWaves () {
        console.log('begin init wave')
        this.waves.nextWave = 0
        this.waves.isActive = false
        // 定义一轮冲击波中外星人从天而降的次序
        this.waves.enemySummonOrder = ['EnemyDummy', 'Enemy1', 'Enemy2', 'Enemy3', 'Boss', 'Boss2']
        console.log('init wave data')
        this.waves.data = [
        // 第一轮冲击波
        {'EnemyDummy': 1, frequency: 10},
        // 第二轮冲击波
        {'EnemyDummy': 8, 'Enemy1': 5, 'Enemy2': 5, 'Boss': 2, frequency: 150},
        // 第三轮冲击波
        {'EnemyDummy': 1, 'Enemy1': 1, 'Enemy2': 1, 'Enemy3': 1, 'Boss': 1, 'Boss2': 30, frequency: 50}
        ]
        console.log('init wave sprite')
        this.nextWaveSprite = new this.assetsLib.WaveCleared()
        this.stage.addChild(this.nextWaveSprite)
        this.nextWaveSprite.x = 999
      },

      //  启动一轮外星人攻击的冲击波
      startWave () {
        this.energies = 120
        this.waves.currentWave = this.waves.data[this.waves.nextWave]
        this.waves.isActive = true
        this.waves.enemiesSummoned = 0
      },

      wavesCleared () {
        this.waves.nextWave += 1
        if (this.waves.nextWave >= this.waves.data.length) {
          this.waves.nextWave = this.waves.data.length - 1
        }
      },

      waveTick () {
        if (!this.waves.isActive) {
          return
        }

        if (this.cjs.Ticker.getTicks() % this.waves.currentWave.frequency === 0) {
          // 依次把不同种类的外星人召唤出来
          var accumunateTargetCount = 0
          for (var i = 0, len = this.waves.enemySummonOrder.length; i < len; i++) {
            var enemyType = this.waves.enemySummonOrder[i]
            // 获得外星人个数
            var targetCount = this.waves.currentWave[enemyType] || 0
            accumunateTargetCount += targetCount
            if (this.waves.enemiesSummoned < accumunateTargetCount) {
              break
            }
          }

          if (this.waves.enemiesSummoned >= accumunateTargetCount) {
            this.waves.isActive = false
          } else {
            this.addEnemy(enemyType)
            this.waves.enemiesSummoned += 1
          }
        }
      },

      stageClick () {
        console.log('stage click')
      },

      tick () {
        if (this.cjs.Ticker.getPaused()) {
          return
        }

        this.livesSpan.textContent = this.lives
        this.energiesSpan.textContent = this.energies
        this.wavesSpan.textContent = this.waves.nextWave + 1

        this.boardTick()
        this.waveTick()
        if (this.waves.isActive === false && this.areEnemiesCleared()) {
          this.boardLayer.isAddingBuilding = false
          this.removeAllBuildings()
          this.removeAllBullets()
          this.wavesCleared()

          // 显示字体动画
          this.cjs.Tween.get(this.nextWaveSprite).to({x: this.canvas.width / 2, y: this.canvas.height / 2, alpha: 0}).to({alpha: 1.0}, 300).wait(1000).to({alpha: 0}, 300).to({x: 999})

          this.startWave()
        }
      },

      removeAllBuildings () {
        for (var i = 0; i < this.boardLayer.cols; i++) {
          for (var j = 0; j < this.boardLayer.rows; j++) {
            this.removeBuilding(this.boardLayer.buildingMap[i][j])
          }
        }

        this.satelliteList = []
        this.castleList = []
      },
      areEnemiesCleared () {
        return this.enemyList.length === 0
      },
      removeAllBullets () {
        this.bulletList.length = 0
        this.effectLayer.removeAllChildren()
      },

      Board () {
        var board = new this.cjs.Container()
        board.x = 10
        board.y = 60

        board.rows = 10
        board.cols = 7

        this.tileWidth = 87
        this.tileHeight = 83

        var sprite = new this.assetsLib.Board()
        board.addChild(sprite)
        sprite.y = board.tileHeight

        // selection对应的就是黄色方框
        board.selection = new this.assetsLib.Selection()
        board.addChild(board.selection)
        board.selection.visible = false

        // 创建二维数组对应页面上的白色网格
        board.isAddingBuilding = false
        board.buildingMap = this.create2DArray(board.cols, board.rows)

        // 让舞台容器响应鼠标移动和点击事件
        this.stage.on('stagemousemove', this.onStageMouseMove)
        this.stage.on('stagemouseup', this.onStageClick)

        return board
      },
      boardTick () {
        // 从敌人对象数组中取出敌人对象，根据属性更新敌人对象在页面上的位置
        this.enemyMap = this.create2DArray(this.boardLayer.cols, this.boardLayer.rows)
        for (var i = 0; i < this.enemyList.length; i++) {
          var enemy = this.enemyList[i]
          var rowCol = this.screenToRowCol(enemy.x, enemy.y)
          this.enemyMap[rowCol.col][rowCol.row] = enemy
          enemy.col = rowCol.col
          enemy.row = rowCol.row
          this.enemyTick(enemy)
        }
        var len = 0
        // 当敌人对象落入到页面底部以后将其从页面上删除
        for (i = 0, len = this.enemyMap.length; i < len; i++) {
          if (this.enemyMap[i][this.boardLayer.rows] !== undefined) {
            var bottomEnemy = this.enemyMap[i][this.boardLayer.rows]
            this.lives -= 1
            this.removeItem(this.enemyList, bottomEnemy)
            if (bottomEnemy.parent) {
              bottomEnemy.parent.removeChild(bottomEnemy)
            }
          }
        }

        this.satelliteTick()
        this.enemyAttackBuilding()

        this.castleTick()

        //  轮询敌人队列和子弹队列，看看两者是否相撞
        for (i = this.enemyList.length - 1; i >= 0; i--) {
          for (var j = this.bulletList.length - 1; j >= 0; j--) {
            var bullet = this.bulletList[j]
            var pos = bullet.localToLocal(0, 0, this.effectLayer)
            rowCol = this.screenToRowCol(pos.x, pos.y)
            if (this.enemyMap[rowCol.col][rowCol.row] !== undefined) {
              var enemyHit = this.enemyMap[rowCol.col][rowCol.row]
              this.enemyDamage(enemyHit, bullet.damageDeal)
              if (enemyHit.hp <= 0) {
                this.enemyMap[rowCol.col][rowCol.row] = undefined
                this.removeItem(this.enemyList, enemyHit)
                this.boardLayer.removeChild(enemyHit)
              }
              this.removeItem(this.bulletList, bullet)
              this.effectLayer.removeChild(bullet)
            }
          }
        }
      },
      addEnemy (enemyClass) {
        var sprite = null
        switch (enemyClass) {
          case 'EnemyDummy':
            sprite = this.enemyDummy()
            break
          case 'Boss':
            sprite = this.boss()
            break
          case 'Boss2':
            sprite = this.boss2()
            break
          case 'Enemy1':
            sprite = this.enemy1()
            break
          case 'Enemy2':
            sprite = this.enemy2()
            break
          case 'Enemy3':
            sprite = this.enemy3()
            break
        }

        if (sprite === null) {
          return
        }

        this.boardLayer.addChild(sprite)
        var col = Math.floor(Math.random() * this.boardLayer.cols)
        var pos = this.rowColToScreen(0, col)
        sprite.x = pos.x
        sprite.y = pos.y
        sprite.row = 0
        sprite.col = col
        this.enemyList.push(sprite)
      },
      addButtonOnMouseDown (e) {
        if (this.cjs.Ticker.getPaused()) {
          return
        }

        // change5
        var buildingType = e.target.dataset.type
        var cost = this.getBuildingCostByType(buildingType)
        if (this.energies >= cost) {
          this.energies -= cost
          this.buildingType = buildingType
          this.readyToPlaceBuilding(buildingType)
        }
      },

      setHud () {
        var addButtons = document.querySelectorAll('.add-button')
        this.livesSpan = document.getElementById('lives')
        this.energiesSpan = document.getElementById('energies')
        this.wavesSpan = document.getElementById('waves')
        for (var i = 0, len = addButtons.length; i < len; i++) {
          var button = addButtons[i]
          button.onmousedown = this.addButtonOnMouseDown
        }
      },

      getBuildingCostByType (type) {
        // change 6
        var building = this.getBuilding(type)
        return building.cost
      },

      readyToPlaceBuilding (type) {
        this.boardLayer.upcomingBuildingType = type
        this.boardLayer.isAddingBuilding = true
      },

      create2DArray (rows, cols, initialValue) {
        var array = []
        for (var i = 0; i < rows; i++) {
          array[i] = []
          for (var j = 0; j < cols; j++) {
            array[i][j] = initialValue
          }
        }

        return array
      },

      building () {
        var b = new this.cjs.Container()
        // change1
        b.cost = 10
        b.shield = 10
        b.damageDeal = 0
        b.attackSpeed = 9999
        return b
      },
      buildingDamage (building, damage) {
        var realDamage = Math.max(damage, building.shield, 0)
        building.hp -= realDamage
        if (building.hp <= 0 && building.parent) {
          this.removeBuilding(building)
        }
      },
      removeBuilding (building) {
        if (building === undefined) {
          return
        }
        this.boardLayer.buildingMap[building.col][building.row] = undefined
        this.boardLayer.removeChild(building)

        // change 11 将建筑物从卫星数组或炮台数组中删除
        for (var i = 0; i < this.satelliteList.length; i++) {
          if (this.satelliteList[i] === building) {
            this.satelliteList.splice(i, 1)
            break
          }
        }
        for (i = 0; i < this.castleList.length; i++) {
          if (this.castleList[i] === building) {
            this.castleList.splice(i, 1)
            break
          }
        }
      },
      enemyAttackBuilding () {
        // 当外星人与道具相遇时，攻击道具
        for (var i = this.enemyList.length - 1; i >= 0; i--) {
          var enemy = this.enemyList[i]
          var row = enemy.row
          var col = enemy.col
          var target
          if (this.boardLayer.buildingMap[col][row] !== undefined) {
            target = this.boardLayer.buildingMap[col][row]
          } else if (this.boardLayer.buildingMap[col][row + 1] !== undefined) {
            target = this.boardLayer.buildingMap[col][row]
          }

          if (target !== undefined) {
            enemy.speed -= enemy.deceleration
            this.enemyStartAttack(enemy, target)
          } else {
            this.enemyStopAttack(enemy)
          }

          target = undefined
        }
      },
      enemyStartAttack (enemy, target) {
        if (!enemy.isAttacking) {
          enemy.isAttacking = true
          enemy.addChild(enemy.attackingSmoke)
        }
        enemy.attackingTarget = target
      },
      enemyStopAttack (enemy) {
        if (enemy.isAttacking) {
          enemy.removeChild(enemy.attackingSmoke)
        }

        enemy.isAttacking = false
        enemy.attackingTarget = undefined
        enemy.speed = enemy.originalSpeed
      },

      satellite () {
        var b = this.building()
        b.addChild(new this.assetsLib.Satellite())
        b.cache(-50, -50, 100, 100)
        b.hp = 150
        b.cost = 30

        b.ticks = 0
        b.energyFrequency = 500
        this.satelliteList.push(b)
        return b
      },

      satelliteTick () {
        for (var i = 0; i < this.satelliteList.length; i++) {
          var satellite = this.satelliteList[i]
          satellite.ticks += 1
          if (satellite.ticks % satellite.energyFrequency === 0) {
            this.summonEnergy(satellite)
          }
        }

        for (i = 0; i < this.energyList.length; i++) {
          this.energyTick(this.energyList[i])
        }
      },

      summonEnergy (satellite) {
        var pos = satellite.localToLocal(0, 0, this.effectLayer)
        var energyObj = this.energy(pos.x, pos.y)
        this.effectLayer.addChild(energyObj)
      },
      satellite2 () {
        var b = this.building()
        b.addChild(new this.assetsLib.Satellite2())
        b.cache(-50, -50, 100, 100)
        b.hp = 150
        b.cost = 600
        // change 7
        b.energyFrequency = 100
        b.ticks = 0
        this.satelliteList.push(b)
        return b
      },

      castle () {
        var b = this.building()
        b.addChild(new this.assetsLib.Castle())
        b.hp = 300
        b.shield = 5
        b.damageDeal = 2
        b.attackSpeed = 120
        // change8
        b.cost = 80
        b.tick = 0

        this.castleList.push(b)

        return b
      },

      castle2 () {
        var b = this.building()
        b.addChild(new this.assetsLib.Castle2())
        b.cache(-50, -50, 100, 100)
        b.hp = 300
        b.shield = 10
        b.damageDeal = 10
        b.attackSpeed = 200
        // change 9
        b.cost = 300
        b.tick = 0
        this.castleList.push(b)
        return b
      },
      castleTick () {
        if (this.cjs.Ticker.getPaused()) {
          return
        }

        for (var i = 0; i < this.castleList.length; i++) {
          var castle = this.castleList[i]
          castle.tick += 1
          if (castle.tick % castle.attackSpeed === 0) {
            this.summonBullet(castle)
          }
        }

        for (i = 0; i < this.bulletList.length; i++) {
          this.bulletTick(this.bulletList[i])
        }
      },
      summonBullet (castle) {
        var bullet = this.bullet(castle.damageDeal)
        // change 10
        bullet.x = Math.abs(castle.x + Math.random() * 20 - 10)
        bullet.y = castle.y
        this.addBullet(bullet)
      },
      addBullet (bullet) {
        this.effectLayer.addChild(bullet)
        this.bulletList.push(bullet)
      },
      spaceJunk () {
        var b = this.building()
        b.addChild(new this.assetsLib.SpaceJunk())
        b.hp = 60
        b.cost = 5
        return b
      },

      screenToRowCol (x, y) {
        var col = Math.floor(x / this.tileWidth)
        var row = Math.floor(y / this.tileHeight)
        return {col: col, row: row}
      },

      rowColToScreen (row, col) {
        var x = this.tileWidth * (col + 0.5)
        var y = this.tileHeight * (row + 0.5)
        return {x: x, y: y}
      },

      getBuilding (buildingClass) {
        if (buildingClass === 'SpaceJunk') {
          console.log('build SpaceJunk')
          return this.spaceJunk()
        }

        if (buildingClass === 'Satellite') {
          console.log('build Satellite')
          return this.satellite()
        }

        if (buildingClass === 'Satellite2') {
          return this.satellite2()
        }

        if (buildingClass === 'Castle') {
          return this.castle()
        }

        if (buildingClass === 'Castle2') {
          return this.castle2()
        }
        return null
      },

      addBuildingAtTile (buildingClass, col, row) {
        var sprite = this.getBuilding(buildingClass)
        this.boardLayer.addChild(sprite)

        var pos = this.rowColToScreen(row, col)
        sprite.x = pos.x
        sprite.y = pos.y

        sprite.row = row
        sprite.col = col
        this.boardLayer.buildingMap[col][row] = sprite
      },

      onStageMouseMove (e) {
        if (!this.boardLayer.isAddingBuilding) {
          return
        }

        var pos = this.boardLayer.globalToLocal(e.stageX, e.stageY)
        var rowCol = this.screenToRowCol(pos.x, pos.y)
        pos = this.rowColToScreen(rowCol.row, rowCol.col)
        this.boardLayer.selection.visible = true

        this.boardLayer.selection.x = pos.x
        this.boardLayer.selection.y = pos.y
      },

      onStageClick (e) {
        if (!this.boardLayer.isAddingBuilding) {
          return
        }

        var pos = this.boardLayer.globalToLocal(e.stageX, e.stageY)
        var rowCol = this.screenToRowCol(pos.x, pos.y)
        var row = rowCol.row
        var col = rowCol.col

        if (row < 0 || row >= this.boardLayer.rows || col < 0 || col >= this.boardLayer.cols) {
          return
        }

        if (this.boardLayer.buildingMap[col][row] === undefined) {
          this.addBuildingAtTile(this.boardLayer.upcomingBuildingType, col, row)
          this.boardLayer.isAddingBuilding = false
          this.boardLayer.selection.visible = false
        }
      },
      removeItem (array, target) {
        for (var k = 0; k < array.length; k++) {
          if (array[k] === target) {
            array.splice(k, 1)
            return true
          }
        }

        return false
      },
      enemy () {
        var enemyObj = new this.cjs.Container()
        enemyObj.originalSpeed = 0.5
        enemyObj.deceleration = 0.4
        enemyObj.hp = 10
        enemyObj.damageDeal = 10
        enemyObj.attackSpeed = 100
        enemyObj.speed = enemyObj.originalSpeed

        enemyObj.isAttacking = false
        enemyObj.attackingTarget = undefined
        enemyObj.attackingSmoke = new this.assetsLib.Explode()
        enemyObj.attackingSmoke.y = 50
        return enemyObj
      },
      enemyTick (enemyObj) {
        if (this.cjs.Ticker.getPaused()) {
          return
        }
        if (enemyObj.speed < 0) {
          enemyObj.speed = 0
        }

        enemyObj.y += enemyObj.speed
        if (this.cjs.Ticker.getTicks() % enemyObj.attackSpeed === 0) {
          if (enemyObj.isAttacking && enemyObj.attackingTarget) {
            this.buildingDamage(enemyObj.attackingTarget, enemyObj.damageDeal)
          }
        }
      },

      enemyDamage (enemy, damage) {
        enemy.hp -= damage
      },
      enemyDummy () {
        var dummy = this.enemy()
        dummy.addChild(new this.assetsLib.Enemy1())
        dummy.originalSpeed = 0.3
        dummy.deceleration = 0.002
        dummy.hp = 1
        dummy.speed = dummy.originalSpeed

        dummy.name = 'dummy'
        return dummy
      },
      boss () {
        var bossEnemy = this.enemy()
        bossEnemy.addChild(new this.assetsLib.Boss())
        bossEnemy.originalSpeed = 0.2
        bossEnemy.deceleration = 0.002
        bossEnemy.hp = 300
        bossEnemy.attackSpeed = 50

        bossEnemy.name = 'boss'
        return bossEnemy
      },
      enemy1 () {
        console.log('enemy1')
        var enemy1 = this.enemy()
        enemy1.addChild(new this.assetsLib.Enemy1())
        enemy1.attackSpeed = 50
        enemy1.speed = enemy1.originalSpeed
        enemy1.name = 'enemy1'

        return enemy1
      },
      enemy2 () {
        console.log('enemy2')
        var enemy2 = this.enemy()
        enemy2.addChild(new this.assetsLib.Enemy2())
        enemy2.originalSpeed = 1.0
        enemy2.attackSpeed = 40
        enemy2.hp = 8
        enemy2.deceleration = 0.008
        enemy2.speed = enemy2.originalSpeed
        enemy2.name = 'enemy2'

        return enemy2
      },
      boss2 () {
        var bossEnemy = this.enemy()
        bossEnemy.addChild(new this.assetsLib.Boss2())
        bossEnemy.originalSpeed = 0.2
        bossEnemy.deceleration = 0.002
        bossEnemy.hp = 300
        bossEnemy.attackSpeed = 50

        bossEnemy.name = 'boss'
        return bossEnemy
      },
      enemy3 () {
        console.log('enemy3')
        var enemy3 = this.enemy()
        enemy3.addChild(new this.assetsLib.Enemy3())
        enemy3.originalSpeed = 3.0
        enemy3.attackSpeed = 10
        enemy3.hp = 8
        enemy3.deceleration = 0.002
        enemy3.speed = enemy3.originalSpeed
        enemy3.name = 'enemy3'

        return enemy3
      },
      energy (x, y) {
        var obj = new this.cjs.Container()
        obj.addChild(new this.assetsLib.Energy())
        obj.cache(-25, -25, 50, 50)
        obj.x = x || 0
        obj.y = y || 0
        // 记录起始x坐标
        obj.baseX = obj.x
        // 膨胀1.2倍再缩回原有尺寸
        this.cjs.Tween.get(obj, {loop: true}).to({scaleX: 1.2, scaleY: 1.2}, 600).to({scaleX: 1.0, scaleY: 1.0}, 600)
        obj.on('mousedown', this.energyClick)
        this.energyList.push(obj)
        return obj
      },
      energyTick (energy) {
        if (this.cjs.Ticker.getPaused()) {
          return
        }
        // 以正玄曲线的方式运动
        var offsetX = Math.sin(this.cjs.Ticker.getTicks() / 10) * 20
        energy.x = energy.baseX + offsetX
        energy.y -= 0.5
      },
      energyClick (e) {
        this.energies += 100
        this.removeItem(this.energyList, e.currentTarget)
        e.currentTarget.parent.removeChild(e.currentTarget)
      },
      bullet (damageDeal) {
        var obj = new this.cjs.Container()
        obj.addChild(new this.assetsLib.Bullet())
        obj.cache(-25, -25, 50, 50)
        obj.damageDeal = damageDeal | 1
        return obj
      },
      bulletTick (bullet) {
        if (this.cjs.Ticker.getPaused()) {
          return
        }

        bullet.y -= 1.5
      }
    }
  }
</script>

<style scoped>
  #game {
    position: relative;
    width:  640px;
    height: 1000px;
    border: 1px solid black;
  }
  #canvas {
    position: absolute;
    top: 0px;
    left: 0px;
    background-color: #94A9B0;
  }

  #hud {
    position: absolute;
    width: 100%;
    height: 60px;
    background: rgba(0,0,0,0.5);
    color: white;
  }

  .add-buttons {
    position: absolute;
    width: 100%;
    height: 60px;
    bottom: 0;
    background: rgba(0, 0, 0, 0.5);
  }

  .add-button {
    display: inline-block;
    width: 50px;
    height: 50px;
    background-color: rgba(255, 255, 255, 0.3);
    color: white;
    text-decoration: none;
    text-align: center;
    line-height: 50px;
    cursor: pointer;
  }

  .add-button:hover {
    background-color: rgba(255, 255, 255, 0.6);
  }
</style>



